@import "_variables.scss";
@import "_histoire.scss";
@import "_home.scss";
@import "_galerie.scss";
@import "_tourisme.scss";


body {
	background-color: #ffffff;
}

h1, h2, h3, h4, h5, h6 { 
    font-style: $font_ibm;
    letter-spacing: 10px;
    font-weight: bold;
}

h1, h2 {
    font-size: 48px;
}

.navbar {
	position: relative;
	z-index: 1;
	background-color: #00000067;
	color: $white;
	a {
		text-decoration: none;
		color: $white;
	}
	a:hover, a.active {
		color: $primary;
	}
}

.navbar-brand img {
    width: 100px;
    margin-left: 3rem;
}

.nav-link {
	font-size: 20px;
    text-transform: uppercase;
    font-family: $font_ibm;
    font-weight: 500;
  
}

.nav-item {
    padding: 0px 20px;
}

.navbar-toggler-icon {
    background-image: url("/assets/images/menu.svg");
}
header {
    position: relative;
    z-index: 0;
}

footer {
	position: relative;
	z-index: 8000 !important;
	background-color: $dark;
	height: 300px;
	font-size: 40px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	box-shadow: 0px -5px 3px 2px rgba(0,0,0,0.35);

	@media screen and (max-width: 992px) {
		flex-direction: column;
	}

	img {
		width: auto;
		height: 150px;
	}
	
	ul {
		display: flex;
		justify-content: space-between;
		align-items: center;
		
		li {
			padding: 20px;
			list-style-type: none;
			color: $white;
			.fab {

				&:hover {
					color: $primary;
				}
			}
		}
	}
	color: $dark;
}
